.task4 {
  font-size: 0.9rem;
  color: gray; }
  .task4__header {
    font-size: 1.2rem;
    margin-bottom: 1rem;
    text-align: center;
    color: #333; }
  .task4__field {
    display: flex;
    flex-wrap: wrap;
    width: 19rem;
    border: 0.1rem solid lightgray; }
  .task4__number {
    width: 1rem;
    padding: 0 0.1rem;
    text-align: center;
    font-size: inherit;
    padding-top: 0.5rem; }
    .task4__number_right-border {
      border: 0.1rem solid lightgray;
      border-width: 0 0.05rem 0 0; }
  .task4__sq {
    height: 2rem;
    width: 2rem;
    border-collapse: collapse;
    border: 0.1rem solid lightgray;
    border-width: 0 0.05rem 0.05rem 0; }
    .task4__sq_black {
      background-color: black; }
    .task4__sq_current {
      background-color: blue; }
    .task4__sq_possible {
      background-color: #59e659; }
  .task4__letter {
    height: 1rem;
    width: 2.05rem;
    text-align: center;
    font-size: inherit;
    padding: 0.1rem 0; }
    .task4__letter_bottom-border {
      border: 0.1rem solid lightgray;
      border-width: 0 0 0.05rem 0; }
